<!-- src/views/ManagerApprovals.vue -->
<template>
  <div class="apv-page">
    <h2 class="apv-title">ÏäπÏù∏ Î∞è ÏöîÏ≤≠ Î™©Î°ù</h2>

    <!-- Í≤ÄÏÉâ/ÌïÑÌÑ∞ -->
    <div class="apv-toolbar">
      <div class="apv-filters">
        <input
          v-model.trim="keyword"
          class="apv-input"
          placeholder="Ïù¥Î¶Ñ/ÏïÑÏù¥Îîî/Í∏∞Í¥ÄÎ™Ö/Ïó∞ÎùΩÏ≤ò/Ïù¥Î©îÏùº Í≤ÄÏÉâ"
          @keyup.enter="fetchList"
        />
        <select v-model="state" class="apv-select" @change="onFilterChange">
          <option value="">Ï†ÑÏ≤¥</option>
          <option value="BA1">ÏöîÏ≤≠</option>
          <option value="BA2">ÏäπÏù∏</option>
          <option value="BA3">Î∞òÎ†§</option>
        </select>
      </div>
      <button class="apv-btn apv-btn-outline" @click="fetchList">Ï°∞Ìöå</button>
    </div>

    <!-- ÌÖåÏù¥Î∏î -->
    <div class="apv-table-wrap">
      <table class="apv-table">
        <thead>
          <tr>
            <th>ÏäπÏù∏ÏΩîÎìú</th>
            <th>Ïù¥Î¶Ñ</th>
            <th>ÏïÑÏù¥Îîî</th>
            <th>Í∏∞Í¥ÄÎ™Ö</th>
            <th>Ïó∞ÎùΩÏ≤ò</th>
            <th>Ïù¥Î©îÏùº</th>
            <th>ÏÉÅÌÉú</th>
            <th>ÏöîÏ≤≠Ïùº</th>
            <th>Ï≤òÎ¶¨Ïùº</th>
            <th>Í¥ÄÎ¶¨</th>
          </tr>
        </thead>
        <tbody>
          <tr v-if="!loading && rows.length === 0">
            <td colspan="10" class="apv-empty">Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏäµÎãàÎã§.</td>
          </tr>
          <tr v-for="r in rows" :key="r.approval_code">
            <td>{{ r.approval_code }}</td>
            <td>{{ r.user_name }}</td>
            <td>{{ r.login_id }}</td>
            <td>{{ r.organization_name }}</td>
            <td>{{ r.phone }}</td>
            <td>{{ r.email }}</td>
            <td>
              <span :class="['apv-state-pill', `apv-state-${r.state}`]">
                {{ stateLabel(r.state) }}
              </span>
            </td>
            <td>{{ fmtDate(r.request_date) }}</td>
            <td>{{ r.approval_date ? fmtDate(r.approval_date) : "-" }}</td>

            <!-- ÏöîÏ≤≠ ÏÉÅÌÉú(BA1)Ïù∏ Í≤ΩÏö∞ÏóêÎßå ÏäπÏù∏/Î∞òÎ†§ Î≤ÑÌäº -->
            <td class="apv-actions-cell">
              <template v-if="r.state === 'BA1'">
                <button
                  class="apv-btn apv-btn-xs apv-btn-primary"
                  @click="onApprove(r)"
                >
                  ÏäπÏù∏
                </button>
                <button
                  class="apv-btn apv-btn-xs apv-btn-danger"
                  @click="onReject(r)"
                >
                  Î∞òÎ†§
                </button>
              </template>
              <template v-else>
                <span class="apv-muted">-</span>
              </template>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ -->
    <div
      v-if="isRejectOpen"
      class="apv-modal-backdrop"
      @click.self="cancelReject"
    >
      <div class="apv-modal">
        <h3 class="apv-modal-title">Î∞òÎ†§ ÏÇ¨Ïú† ÏûÖÎ†•</h3>
        <p class="apv-modal-sub">
          ÏäπÏù∏ÏΩîÎìú: <b>{{ rejectTarget?.approval_code }}</b> &nbsp;/ Ïã†Ï≤≠Ïûê:
          <b>{{ rejectTarget?.user_name }}</b>
        </p>

        <textarea
          v-model.trim="rejectReason"
          rows="4"
          class="apv-textarea"
          placeholder="Î∞òÎ†§ ÏÇ¨Ïú†Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî."
        ></textarea>

        <div class="apv-modal-actions">
          <button class="apv-btn" @click="cancelReject">Ï∑®ÏÜå</button>
          <button class="apv-btn apv-btn-primary" @click="confirmReject">
            Î∞òÎ†§ ÌôïÏ†ï
          </button>
        </div>
      </div>
    </div>

    <!-- ÌéòÏù¥Ïßï -->
    <div class="apv-pagination">
      <button
        class="apv-btn apv-btn-xs"
        :disabled="page <= 1 || loading"
        @click="goPage(page - 1)"
      >
        Ïù¥Ï†Ñ
      </button>
      <span class="apv-page-text">{{ page }}</span>
      <button
        class="apv-btn apv-btn-xs"
        :disabled="rows.length < size || loading"
        @click="goPage(page + 1)"
      >
        Îã§Ïùå
      </button>
    </div>

    <div v-if="error" class="apv-error" role="alert">
      {{ error }}
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from "vue";
import axios from "axios";
import { useAuthStore } from "@/store/authLogin";
import { useRouter } from "vue-router";

const auth = useAuthStore();
const router = useRouter();

onMounted(() => {
  auth.reload(); // ÏÉàÎ°úÍ≥†Ïπ® Ïãú Î°úÍ∑∏Ïù∏ Î≥µÏõê

  if (auth.role !== "AA4") {
    alert("Ï†ëÍ∑º Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§.");
    router.push("/");
  }
});

/* ÏÉÅÌÉú */
const rows = ref([]);
const loading = ref(false);
const error = ref("");
// Î∞òÎ†§ Î™®Îã¨Ïö© ÏÉÅÌÉú
const isRejectOpen = ref(false);
const rejectReason = ref("");
const rejectTarget = ref(null);

/* ÌïÑÌÑ∞/ÌéòÏù¥ÏßÄ */
const state = ref(""); // '', 'BA1', 'BA2', 'BA3'
const keyword = ref("");
const page = ref(1);
const size = ref(20);

/* Ìó¨Ìçº */
function stateLabel(s) {
  switch (s) {
    case "BA1":
      return "ÏöîÏ≤≠";
    case "BA2":
      return "ÏäπÏù∏";
    case "BA3":
      return "Î∞òÎ†§";
    default:
      return s || "-";
  }
}
function fmtDate(d) {
  if (!d) return "-";
  const date = new Date(d);
  if (isNaN(date.getTime())) return "-";
  const yyyy = date.getFullYear();
  const mm = String(date.getMonth() + 1).padStart(2, "0");
  const dd = String(date.getDate()).padStart(2, "0");
  return `${yyyy}-${mm}-${dd}`;
}

/* Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ */
async function fetchList() {
  loading.value = true;
  error.value = "";
  try {
    const res = await axios.get("/api/approvals", {
      params: {
        type: "ORG_MANAGER",
        state: state.value,
        keyword: keyword.value,
        page: page.value,
        size: size.value,
      },
    });
    rows.value = Array.isArray(res.data?.data) ? res.data.data : [];
  } catch (e) {
    console.error(e);
    error.value = "Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.";
    rows.value = [];
  } finally {
    loading.value = false;
  }
}

/* ÌéòÏù¥ÏßÄ Ïù¥Îèô */
function goPage(p) {
  if (p < 1) return;
  page.value = p;
  fetchList();
}
function onFilterChange() {
  page.value = 1;
  fetchList();
}

/* ÏäπÏù∏/Î∞òÎ†§ Î≤ÑÌäº ÌÅ¥Î¶≠ Ìï∏Îì§Îü¨ */
async function onApprove(row) {
  if (!confirm(`[${row.approval_code}] ${row.user_name} ÏäπÏù∏ Ï≤òÎ¶¨Ìï†ÍπåÏöî?`))
    return;
  try {
    await axios.put(
      `/api/approvals/${encodeURIComponent(row.approval_code)}/approve`,
      {
        // üîπ Ï≤òÎ¶¨Ïûê ÏΩîÎìú Ï∂îÍ∞Ä
        processorCode: auth.userCode,
      }
    );
    alert("ÏäπÏù∏ Ï≤òÎ¶¨ÎêòÏóàÏäµÎãàÎã§.");
    await fetchList();
  } catch (e) {
    console.error(e);
    alert("ÏäπÏù∏ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
  }
}

async function onReject(row) {
  rejectTarget.value = row;
  rejectReason.value = "";
  isRejectOpen.value = true;
}

function cancelReject() {
  isRejectOpen.value = false;
  rejectTarget.value = null;
  rejectReason.value = "";
}

async function confirmReject() {
  if (!rejectTarget.value) return;
  if (!rejectReason.value.trim()) {
    alert("Î∞òÎ†§ ÏÇ¨Ïú†Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
    return;
  }

  try {
    await axios.put(
      `/api/approvals/${encodeURIComponent(
        rejectTarget.value.approval_code
      )}/reject`,
      {
        reason: rejectReason.value,
        // üîπ Ï≤òÎ¶¨Ïûê ÏΩîÎìú Ï∂îÍ∞Ä
        processorCode: auth.userCode,
      }
    );
    alert("Î∞òÎ†§ Ï≤òÎ¶¨ÎêòÏóàÏäµÎãàÎã§.");
    isRejectOpen.value = false;
    rejectTarget.value = null;
    rejectReason.value = "";
    await fetchList();
  } catch (e) {
    console.error(e);
    alert("Î∞òÎ†§ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
  }
}

onMounted(fetchList);
</script>

<style scoped>
* {
  font-size: 15px;
}

.apv-page {
  max-width: 1600px;
  margin: 24px auto;
  padding: 0 16px 40px;
}

.apv-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 16px;
  color: #111827;
}

/* üîπ ÏÉÅÎã® Ìà¥Î∞î (Í≤ÄÏÉâ/ÌïÑÌÑ∞ ÎùºÏù∏) */
.apv-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
  flex-wrap: wrap;
}

.apv-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.apv-input,
.apv-select {
  min-width: 220px;
  padding: 7px 10px;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  font-size: 13px;
  outline: none;
  background: #fff;
}

.apv-input:focus,
.apv-select:focus {
  border-color: #7ea6f6;
  box-shadow: 0 0 0 1px rgba(126, 166, 246, 0.25);
}

/* üîπ Î≤ÑÌäº Í≥µÌÜµ */
.apv-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 6px 12px;
  border-radius: 999px;
  border: 1px solid #d2d6e0;
  background: #ffffff;
  font-size: 12px;
  cursor: pointer;
  transition:
    background-color 0.12s ease,
    transform 0.06s ease,
    box-shadow 0.12s ease;
  white-space: nowrap;
}

.apv-btn:hover {
  background: #f3f4ff;
  box-shadow: 0 1px 3px rgba(15, 23, 42, 0.08);
  transform: translateY(-0.5px);
}

.apv-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ÏûëÏùÄ Î≤ÑÌäº */
.apv-btn-xs {
  padding: 4px 8px;
  font-size: 11px;
}

/* Î≤ÑÌäº Î≥ÄÌòï */
.apv-btn-primary {
  background: #7ea6f6;
  border-color: #7ea6f6;
  color: #fff;
}

.apv-btn-primary:hover {
  background: #678fe0;
}

.apv-btn-danger {
  background: #f76c6c;
  border-color: #f76c6c;
  color: #fff;
}

.apv-btn-danger:hover {
  background: #e25656;
}

.apv-btn-outline {
  background: #ffffff;
  border-color: #7ea6f6;
  color: #315fbf;
}

/* üîπ ÌÖåÏù¥Î∏î Ïπ¥Îìú (ÏßÄÏõê/ÌõÑÏõê Í≤∞Í≥º Ïπ¥ÎìúÎûë ÌÜµÏùº) */
.apv-table-wrap {
  background: #ffffff;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 4px rgba(15, 23, 42, 0.06);
  padding: 12px 16px;
  overflow-x: auto;
}

/* üîπ ÌÖåÏù¥Î∏î Í∏∞Î≥∏ Ïä§ÌÉÄÏùº (priority-table ÎäêÎÇå) */
.apv-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;
}

.apv-table thead th {
  text-align: center;
  padding: 10px 8px;
  font-weight: 600;
  font-size: 12px;
  color: #6b7280;
  border-bottom: 1px solid #e5e7eb;
  background: #f9fafb;
  white-space: nowrap;
}

.apv-table tbody td {
  padding: 9px 8px;
  border-bottom: 1px solid #f3f4f6;
  color: #374151;
  vertical-align: middle;
  text-align: center;
}

/* Ìñâ Ìò∏Î≤Ñ */
.apv-table tbody tr {
  transition:
    background-color 0.12s ease,
    transform 0.06s ease;
}

.apv-table tbody tr:hover {
  background: #f3f4ff;
  transform: translateY(-1px);
}

.apv-empty {
  text-align: center;
  padding: 14px 0;
  color: #9ca3af;
}

/* üîπ ÏÉÅÌÉú Pill (ÌõÑÏõê/ÏßÄÏõê Î¶¨Ïä§Ìä∏Îûë ÎèôÏùº ÌÜ§) */
.apv-state-pill {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 500;
  border: 1px solid transparent;
}

.apv-state-BA1 {
  background: #eef2ff;
  border-color: #c7d2fe;
  color: #3730a3;
}

.apv-state-BA2 {
  background: #ecfdf5;
  border-color: #bbf7d0;
  color: #166534;
}

.apv-state-BA3 {
  background: #fef2f2;
  border-color: #fecaca;
  color: #b91c1c;
}

/* ÏäπÏù∏/Î∞òÎ†§ Î≤ÑÌäº Ïπ∏ */
.apv-actions-cell {
  display: flex;
  justify-content: center;
  gap: 4px;
  align-items: center;
}

.apv-muted {
  color: #9ca3af;
  font-size: 12px;
}

/* üîπ Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ */
.apv-modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.45);
  display: grid;
  place-items: center;
  z-index: 9999;
}

.apv-modal {
  width: min(460px, 92vw);
  background: #ffffff;
  border-radius: 12px;
  padding: 18px 18px 16px;
  box-shadow: 0 8px 22px rgba(15, 23, 42, 0.35);
  border: 1px solid #e2e7f0;
}

.apv-modal-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 6px;
  color: #111827;
}

.apv-modal-sub {
  font-size: 13px;
  color: #4b5563;
  margin-bottom: 10px;
}

.apv-textarea {
  width: 100%;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  padding: 8px 10px;
  font-size: 13px;
  resize: vertical;
  min-height: 90px;
}

.apv-textarea:focus {
  outline: none;
  border-color: #7ea6f6;
  box-shadow: 0 0 0 1px rgba(126, 166, 246, 0.25);
}

.apv-modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  margin-top: 12px;
}

/* üîπ ÌéòÏù¥Ïßï/ÏóêÎü¨ */
.apv-pagination {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 8px;
  margin-top: 12px;
  font-size: 12px;
  color: #4b5563;
}

.apv-page-text {
  font-size: 13px;
}

.apv-error {
  margin-top: 8px;
  color: #b91c1c;
  font-size: 12px;
}
</style>
